grid;\n  grid-template-columns: repeat(auto-fit, minmax(calc(100% / 4), auto));\n  row-gap: 15px;\n  margin-bottom: 40px;\n}\n\n.selected {\n  color: black !important;\n}\n\n.result--event {\n  @include medium_kor;\n  color: #5050508f;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n  padding-right: 10px;\n  text-align: center;\n  span {\n    border-bottom: 1px solid $darkgrey_light;\n\n    &:hover {\n      cursor: pointer;\n    }\n  }\n}\n\n.result--table {\n  position: relative;\n  display: flex;\n  justify-content: space-around;\n  background-color: #b2babb3d;\n  padding: $table_padding;\n  p {\n    color: $darkgrey_default;\n  }\n}\n\n.result--submit {\n  background-color: #b2babb3d;\n  padding: 30px 100px;\n  position: relative;\n}\n\n.result--submit--delete_event {\n  position: absolute;\n  right: 15px;\n  top: 10px;\n  width: 15px;\n  &:hover {\n    cursor: pointer;\n  }\n}\n\n.test {\n  border: 1px solid;\n}\n\n.result--submit--event_title {\n  @include medium_kor;\n  // font-weight: bold;\n}\n\n.result--submit--event_description {\n  @include medium_kor;\n  color: $darkgrey_medium;\n}\n\n.result--submit--event_bot_line {\n  border: 0;\n  height: 0.5px;\n  background-color: $darkgrey_light;\n  // border: 0.5px solid $darkgrey_light;\n}\n\n.result--submit--info {\n  @include medium_kor;\n}\n\n.result--submit--form_wrapper {\n  text-align: end;\n}\n\n.result--submit--input {\n  width: 40px;\n  border: none;\n  border-bottom: 1px solid $darkgrey_default;\n  font-size: $default_medium_15;\n  text-align: center;\n  background: none;\n  &:focus {\n    outline: none;\n  }\n}\n\n.result--submit--label {\n  @include medium_kor;\n}\n\n.result--submit--button {\n  font-family: $kor_font_regular;\n  font-size: 16px;\n  color: $red_for_submit;\n  background: none;\n  border: none;\n  &:hover {\n    cursor: pointer;\n  }\n}\n\n.result--team_name {\n  @include medium_eng;\n  font-weight: 500;\n  font-size: $sub_title_18;\n  text-align: center;\n}\n\n.result--intra {\n  @include medium_eng;\n  text-align: center;\n}\n\n.result--hr {\n  position: absolute;\n  width: 600px;\n  top: 60px;\n  border: 0;\n  height: 0.5px;\n  background-color: $darkgrey_light;\n}\n\n.result--no_attend {\n  @include medium_kor;\n  font-size: 16px;\n  margin: 30px 0;\n}\n","@import '@css/fonts.scss';\n\n$basic_width: 700px;\n\n@mixin basic--wrappers {\n  width: $basic_width;\n  margin: 0 auto;\n}\n","// family\n$kor_font_regular: 'Noto Sans KR', sans-serif;\n$eng_font_regular: 'Roboto', sans-serif;\n\n// color\n$darkgrey_darker: #252323;\n$darkgrey_default: rgb(80, 80, 80);\n$darkgrey_medium: rgb(140, 140, 140);\n$darkgrey_light: rgb(184, 184, 184);\n$lightblue: #73b2ed;\n$pinkred: #ec7063;\n$red_for_submit: #9f062ba3;\n\n//size\n$main_title_22: 22px;\n$sub_title_18: 18px;\n$default_medium_15: 15px;\n$default_small_13: 13px;\n\n// font-set\n@mixin medium_kor {\n  font-family: $kor_font_regular;\n  font-size: $default_medium_15;\n  color: $darkgrey_default;\n}\n\n@mixin small_kor {\n  font-family: $kor_font_regular;\n  font-size: $default_small_13;\n  color: $darkgrey_default;\n}\n\n@mixin medium_eng {\n  font-family: $eng_font_regular;\n  font-size: $default_medium_15;\n  color: $darkgrey_default;\n}\n\n@mixin small_eng {\n  font-family: $eng_font_regular;\n  font-size: $default_small_13;\n  color: $darkgrey_default;\n}\n","@import '@css/fonts.scss';\n\n@mixin main--wrappers {\n  width: 600px;\n  margin: 0 auto;\n}\n\n@mixin main--titles {\n  // font-weight: 300;\n  font-family: $kor_font_regular;\n  font-size: 16px;\n  color: $darkgrey_default;\n}\n"],"sourceRoot":""}]);
